/*===== Separaciones =====*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: #0A0A16;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url(../img/bgTel.png);
}
@media (min-width: 480px) {
  body {
    background-image: url(../img/bgTablet.png);
  }
}
@media (min-width: 768px) {
  body {
    background-image: url(../img/bgDesktop.png);
  }
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 2.5rem;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.barra {
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
}
.barra .separador {
  color: #fff;
}
.barra img {
  margin: 0 auto;
  padding-bottom: 2rem;
  width: 20rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  padding-bottom: 2rem;
}
.navegacion span {
  display: none;
}
@media (min-width: 768px) {
  .navegacion span {
    margin: 2rem;
    visibility: visible;
    user-select: none;
    display: inline;
  }
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    padding-bottom: 2rem;
  }
}
.navegacion a {
  color: #fff;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
  .navegacion a:first-of-type {
    margin-left: 2rem;
  }
}
.navegacion a:hover {
  color: #9B5B90;
}

.contenido-inicio h1, .contenido-inicio h2, .contenido-inicio p {
  color: #fff;
}
.contenido-inicio h1 {
  font-size: 5rem;
}
@media (min-width: 768px) {
  .contenido-inicio h1 {
    font-size: 7rem;
  }
}
.contenido-inicio h2 {
  font-size: 4rem;
}
@media (min-width: 768px) {
  .contenido-inicio h2 {
    font-size: 6rem;
  }
}
.contenido-inicio p {
  text-align: center;
}
.contenido-inicio img {
  margin: 0 auto;
  max-width: 30rem;
}

.servicios {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .servicios {
    flex-direction: row;
    text-align: center;
  }
}
.servicios .servicio img {
  width: 15rem;
}
.servicios .servicio h3 {
  color: #fff;
}